import * as React from "react"
import Layout from "../components/Layout"
import WaveHeader from "../components/helpers/WaveHeader" 
import { useStaticQuery, graphql } from "gatsby" 
import {Col,Container,Row ,Card} from 'react-bootstrap'
import {Link} from "gatsby"
import Seo from '../components/seo/Seo'; 
const Html = ({pageContext}) => {
  const data = useStaticQuery(graphql`
  query clientStartForm{ 
      site {  
        siteMetadata{ 
            forms {  
              clienteNuevo
              cliente
              registro
              contacto
              sugerencias
            }
          } 
      }
    }
  `) 
  const seo = {
    frontmatter: { 
      title:"Area de pedidos para clientes y nuevos clientes ",
      ...pageContext.frontmatter,
    }, 
  };

  return ( 
    <Layout> 
     <Seo {...seo} /> 
     <WaveHeader fill="#00000"/>
     <Container>
       <div className="my-5 py-5 text-center" style={{
         fontFamily:"Arial"
       }}>
          <h1>Bienvenido al restaurante <br />El dicho</h1>
          <p>Puedes realizar tu pedido con dias de anticipación, déjanos tus datos para comunicarnos contigo. Atendemos de lunes a sabado</p>    
       </div>
      <Row className="text-center row align-items-center">
          <Col className="col-12 col-md-6 py-5">
            <Card> 
              <Card.Body>
                <Card.Title>Inscribirse</Card.Title>
                <Card.Text>
                   Si eres nuevo y te gustaria unirte a nuestro club, registrate a traves de nuestro formulario, 
                </Card.Text>
              </Card.Body>
              <Link to="/cliente/inscripcion" className="btn  btn-lg btn-success">Inscribirme</Link>
            </Card>
          </Col>
          <Col className="col-12 col-md-6 py-5">
            <Card> 
              <Card.Body>
                <Card.Title>Realizar un pedido</Card.Title>
                <Card.Text>
                  Si ya eres miembro de nuestro club y cliente recurrente usa este formulario para realizar tu pedido.
                </Card.Text>
              </Card.Body>
              <Link to="/cliente/recurrente" className="btn  btn-lg btn-primary">Realizar Pedido</Link>
            </Card>
          </Col>
          <Col className="col-12 col-md-6  py-5">
            <Card> 
              <Card.Body>
                <Card.Title>Soy Cliente nuevo</Card.Title>
                <Card.Text>
                   Si es tu primera vez solicitando tu menu o algún otro producto por este medio utiliza el siguiente formulario. 
                </Card.Text>
              </Card.Body>
              <Link to="/cliente/nuevo" className="btn  btn-lg btn-primary">Realizar Pedido</Link>
            </Card>
          </Col>
          <Col className="col-12 col-md-6 py-5">
            <Card>  
              <Card.Body>
                <Card.Title>Somos Beta</Card.Title>
                <Card.Text>
                 Estamos innovando para ofrecerte un mejor servicio con experiencias únicas, Únete al club!.
                </Card.Text> 
              </Card.Body>
              <Link to="/sugerencias" className="btn  btn-lg btn-danger">Sugerencias</Link>
            </Card>
          </Col>
     
      </Row>
      <div className="my-5 d-inline-block">
        <h2>Nos preocupamos por tu salud</h2>
        <p>Consideramos que la alimentación es parte importante de tu salud. Evitamos usar productos industrializados. Nuestro principal objetivo es ofrecer un menu saludable con productos frescos directamente de la chacra a tu paladar.</p>
      </div>
    </Container> 
    </Layout>
  )
}

export default Html
 